import blog from '@/api/blog'
import { mapGetters} from 'vuex'


export default {
  data () {
    return {
      blogs:[],
      page:1, 
      total:0
    }
  },
  computed:{
    ...mapGetters(['user'])
  },
  created(){
    // this.userId = this.$route.params.userId
    this.page = parseInt( this.$route.query.page) || 1  //根据url默认去到指定页
    console.log('here')
    blog.getBlogsByUserId(this.user.id,{ page:this.page })
      .then((res)=>{
        this.page  = res.page
        this.total = res.total
        this.blogs = res.data
        console.log(this.page)
      })
  },
  methods:{
    splitDate(data){
      let dateObj = typeof data === 'object' ? data : new Date(data)
      return {
        date: dateObj.getDate(),
        month: dateObj.getMonth()+1,
        year: dateObj.getFullYear()
      }
    },
    async onDelete(blogId){
      await this.$confirm('此操作将永久删除该文件，是否继续？', '提示' ,{
        confirmButtonText: '确定' ,
        cancelButtonText: '取消',
        type: 'warning'
      })
      await blog.deleteBlog({blogId})
      this.$message.success('删除成功')
      this.blogs = this.blogs.filter(blog => blog.id !==blogId)
    }
    ,
    // onDelete(blogId){

  
    //       this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
    //         confirmButtonText: '确定',
    //         cancelButtonText: '取消',
    //         type: 'warning'
    //       }).then(() => {
    //         blog.deleteBLog({blogId}).then(()=>{
    //           this.$message({
    //             type: 'success',
    //             message: '删除成功!'
    //           });
    //         })
    //       })

    // },
    onPageChange(a){
      blog.getBlogsByUserId(this.user.id,{ page: a}).then((res)=>{
        this.blogs = res.data
        this.total = res.total
        this.page = res.page
        this.$router.push({ path:'/my', query: {page: a}})
        console.log(this.page)
      })
    }
  }
}


